nav {
    width: 100vw;
    height: auto;
    background-color: #1a2b3c;
    color: whitesmoke;
    padding: 1rem;
    display: flex;
    align-items: center;
}

/* -------------------LOGO ----------------- */
.nav-logo {
    flex: 1;
    font-family: 'Titan One', cursive;
    font-size: 1.5rem;
}

/* -------------------LIST----------------- */
.nav-list {
    flex: 3;
}

.nav-list>ul {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
}

/* -------------------OPTIONS----------------- */
.nav-options {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    gap: 4em;
}

.nav-darkMode {
    width: 24px;
    aspect-ratio: 1;
}

.nav-option {
    width: 20px;
    /* height: 20px; */
}

@media all and (max-width:570px) {
    .nav-list {
        display: none;
    }

    .nav-logo {
        flex: 4;
    }

    .nav-options {
        flex: 1;
        gap: 2rem;
    }

}